@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,700;1,400;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,700;1,400;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,700;1,400;1,700&display=swap);
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  background-color: #f8b229;
  font-family: 'Roboto', sans-serif;
}

.w100 {
  width: 100%;
  max-width: 100%;
  max-height: 300px;
}

.margin-bottom--8 {
  margin-bottom: 8px;
}

.margin-bottom--16 {
  margin-bottom: 16px;
}

.margin-bottom--24 {
  margin-bottom: 24px;
}

.about{text-align:left}.about h1{text-align:center;color:#165b33}

.Card{max-width:300px;border-radius:7px;box-shadow:0 5px 25px rgba(0,0,0,0.2);background-color:#ffffff;padding:16px;margin:16px auto;display:flex;flex-direction:column;justify-content:space-between}.Card__image-wrapper{width:100%;padding-top:100%;position:relative;border-radius:7px;box-shadow:0 5px 25px rgba(0,0,0,0.2)}.Card__image{width:100%;border-radius:7px;position:absolute;left:0;top:0}.Card__description-wrapper{margin-bottom:8px}.Card__heading{color:#165B33;font-size:28px;font-weight:700;margin-bottom:4px}.Card__subheading{color:#146B3A;font-size:20px;text-transform:uppercase;font-weight:400;margin-bottom:4px;letter-spacing:0.2em}.Card__paragraph{color:#000000;font-size:16px;text-align:justify;margin-bottom:8px}.Card__purchase-wrapper{display:flex;justify-content:space-around;align-items:center}.Card__price{font-size:32px;font-weight:bold;color:#BB2528}@media only screen and (max-width: 600px){.Card__heading{text-align:center;margin-bottom:2px}.Card__subheading{text-align:center;margin-bottom:2px}.Card__purchase-wrapper{display:flex;flex-direction:column;justify-content:space-around;align-items:center}}

.product-section{width:100%;display:flex;justify-content:flex-start;flex-wrap:wrap}

.label{font-family:'Roboto', sans-serif;font-weight:bold;font-size:16px;padding:10px;color:#165b33}.input{width:100%;max-width:100%;min-height:40px;max-height:300px;border-radius:3px;border:2px solid #ea4630;box-shadow:0 5px 25px rgba(0,0,0,0.2);outline:none;color:#165b33;font-family:'Roboto', sans-serif;font-size:16px;padding:10px;margin:10px}.input.textarea{min-height:80px;min-width:100%}button{padding:15px 30px;border-radius:3px;outline:none;font-family:'Roboto', sans-serif;font-weight:bold;font-size:16px;border:none;background-color:#146b3a;box-shadow:0 5px 25px rgba(0,0,0,0.2);color:#ffffff;cursor:pointer;transition:background-color 0.2s}button:hover{background-color:#ea4630}

.nav{margin-right:20px;text-decoration:none;text-transform:uppercase;font-size:20px;color:#244c2a;transition:color 0.2s}.nav:last-child{margin-right:0}.nav:hover{color:#ea4630}.selected{font-weight:bold;color:#6e040e}

.box select{background-color:#BB2528;font-family:'Roboto', sans-serif;font-weight:700;color:white;padding:5px;max-width:100%;border:none;border-radius:3px;font-size:16px;box-shadow:0 5px 25px rgba(0,0,0,0.2);outline:none;cursor:pointer}.box select:hover{background-color:#EA4630}

.header{padding:30px 0}

